<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process225"></a>

<p><br></p>
<p><span class="rvts17">邪恶之物的猎人（</span><span class="rvts50">A Hunter of Wicked Thing</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:293,Y:284</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：贾韩（</span><span class="rvts20">Jahan</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉提倡者，了解真正的恶魔的名字</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1296.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">贾韩位于回廊树林西边的一处低势丛林里。与他交谈，他会让你杀掉血月岛的提倡者。</span>
<br><span class="rvts21">提倡者是血月岛的最高领主，在杀掉他之前，可以先完成提倡者交给你的</span><span class="rvts20"></span><span class="rvts21">提倡者（</span><span class="rvts20">The Advocate</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线，清理占领在先祖之树那里的黑环士兵。之后提倡者会到先祖之树那里。最后杀掉他，并向贾韩汇报。</span>
<br><span class="rvts21">接着，贾韩会让你寻找困扰血月岛先祖之树的恶魔的名字。接着再次拜访血月岛图书馆，获得</span><span class="rvts20"></span><span class="rvts21">档案保管员的日志（</span><span class="rvts20">Archivist's Journal</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，阅读之后，会了解古树的名字。回到古树，尝试呼唤它的名字来减轻它的痛苦，之后古树会告诉你摧残它的恶魔的名字。</span>
<br><span class="rvts21">再次找到贾韩，说出恶魔的名字，贾韩会教你提升</span><span class="rvts20">1</span><span class="rvts21">点的源力最大值，并教会你</span><span class="rvts20"></span><span class="rvts21">召唤内心的恶魔（</span><span class="rvts20">Summon Inner Demon</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的技能。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1297.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在贾韩屋子内，可以找到一本《华丽的赞美诗集》，使用队伍中具备</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">标签的角色阅读它，可以破解血月岛上的</span><span class="rvts20">3</span><span class="rvts21">处被诅咒的雕像，从而完成对应的</span><span class="rvts20"></span><span class="rvts21">受苦者（</span><span class="rvts20">Bound By Pain</span><span class="rvts21">），宁静者（</span><span class="rvts20">The Silent One</span><span class="rvts21">），勇士的错觉（</span><span class="rvts20">The Delusion of Grandeur</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">三个支线任务。</span>
<br><span class="rvts21">贾韩屋外面关着两个血月岛成员，千万不要让他俩死掉，之后将会影响贾韩的驱魔仪式，并无法触发让贾韩登上复仇女神号的选项。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



